.home {
    margin-bottom: 40px;
}

.home .hero {
    display: flex;
    gap: 10px;
    align-items: center;
    margin-bottom: 12px;
    color: #727274;
}

.hero-details p{
    font-family: 'Neucha', cursive,"Trueno";
    font-size: 16px;
    font-weight: 300;
}

.hero-details h3{
    font-family: 'Neucha', cursive,"Trueno";
    font-size: 20px;
    font-weight: 700;
}

.account-details-wrapper {
    background-image: url("../assets/wallpaper.png");
    background-repeat: no-repeat;
    border-radius:12px;
    width: 100%;
    height: 200px;
    color: #fff;
    display: flex;
    align-items: center;
}

/* .account-details-wrapper.collapse {
    width: 1090px;
    padding: 12px;
} */

.account-details-contents {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 55px;
}

.account-details-content1 {
    margin: 0 40px;
}

.account-details-content1 p {
    font-family: 'Neucha', cursive,"Trueno"; 
    font-size: 14px;
    margin-bottom: 12px;
}

.account-details-content1 h4 {
    font-size: 20px;
}

@media only screen and (max-width: 994px) {
    .account-details-wrapper {
        width: 650px;
    }

    .account-details-wrapper.collapse {
    width: 750px;
}

.account-details-contents {
    margin-left: 15px;
}

.account-details-content1 {
    margin: 0 20px;
}

.account-details-content1 p {
    font-size: 12px;
    margin-bottom: 10px;
}

.account-details-content1 h4 {
    font-size: 18px;
}
}