.card-wrapper {
    width: 100%;
    height: 200px;
    margin-top: 50px;
    display: flex;
}

/* .card-wrapper.collapse {
    width: 1200px;
} */

.card {
    margin: 0 10px;
}

.card-wrapper.collapse .card{
    margin: 0 30px;
}


.card-content {
    position: relative;
    padding: 15px;
    height: 190px;
    width: 291px;
    border-radius: 15px;
    color: #fff;
}

.card-wrapper.collapse .card-content{
    padding: 15px;
    height: 190px;
    width: 331px;
}

.card-content h3{
    font-family: 'Neucha', cursive,"Trueno";
    font-size: 16px;
}

.card-content p{
    font-family: 'Neucha', cursive,"Trueno";
    font-size: 10px;
}

.card-content.awards {
    background: #E2698D;
}

.card-content.garden {
    background: #265859;
}

.card-content.women {
    background: #F2F1F3;
    color: #E2698D;
}

.card-content.women p{
    color: #265859;
}

.card-content p{
    margin-top: 10px;
}

.card-content.awards img{
    position: absolute;
    top: 18px;
    right: 0px;
}
.card-content.garden img{
    position: absolute;
    top: 50px;
    right: 0;
}

.card-content.women img{
    position: absolute;
    top: 25px;
    right: 15px;
}

.card-content.women p{
    margin-bottom: 30px;
}

.card-btn {
    padding: 5px;
    color: #265859;
    border: none;
    font-family: 'Neucha', cursive,"Trueno";
    font-size: 14px;
    outline: none;
}

.card-btn.fill {
    background: #E2698D;
    font-family: 'Neucha', cursive,"Trueno";
    font-weight: 700;
    color: #fff;
    font-size: 16px;
    border-radius: 5px;
    margin-right: 2px;
}

.card-btn:hover {
    background: #E2698D;
    border-radius: 5px;
    font-size: 16px;
    color: #fff;
}
.card-btn.fill:hover {
    background: #ec2964;
}

@media only screen and (max-width: 994px) {
    .card-wrapper {
        width: 650px;
        background: red;
    }

    .card {
        margin: 0 5px;
    }

    .card-wrapper.collapse .card{
        margin: 0 10px;
    }

    .card-content {
        padding: 15px;
        height: 170px;
        width: 205px;
    }

    .card-content h3{
        font-size: 12px;
    }
    
    .card-content p{
        font-size: 8px;
    }

    .card-wrapper.collapse .card-content{
        padding: 15px;
        height: 170px;
        width: 234px;
    }

    .card-content.awards img{
        height: 150px;
        width: 230px;
        top: 20px;
        right: -55px;
    }

    .card-content.garden img{
        height: 90px;
        width: 180px;
        top: 60px;
        right: -30px;
    }

    .card-content.women img{
        height: 100px;
        width: 190px;
        top: 25px;
        right: -35px;
    }
    
    .card-content.women p{
        margin-bottom: 40px;
    }
    
    .card-btn {
        padding: 3px;
        font-size: 12px;
    }
    
    .card-btn.fill {
        font-size: 12px;
        margin-right: 1px;
    }
}